import React, {ButtonHTMLAttributes, DetailedHTMLProps} from "react";
import s from "./SuperButton.module.css";

//    , children        
type DefaultButtonPropsType = DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>;

type SuperButtonPropsType = DefaultButtonPropsType & {
    red?: boolean
    title?:string
    onClick?: () => void
    onChange?:any
    password?:string
    resetPasswordsToken?:string
    disabled?:boolean
}

const SuperButton: React.FC<SuperButtonPropsType> = (
    {
        red, className,title,onChange,onClick,disabled,
        ...restProps//       restProps,    children
    }
) => {
    //const finalClassName = `${red ? s.red : s.default} ${className}`;
    //const finalClassName={ props.error? s.buttonErrorTrue : s.buttonErrorFalse}

    const onClickButon = () =>{
        onClick && onClick()
    }


    return (
        <button
            disabled={disabled}
            className={red? s.buttonErrorTrue : s.buttonErrorFalse }
            //className={finalClassName}
            {...restProps} //        (children  )
            onClick={onClickButon}
        >{title}</button>
    );
}

export default SuperButton;
