import React, {
    ChangeEvent,
    DetailedHTMLProps,
    InputHTMLAttributes,
    KeyboardEvent,
} from "react";
import s from "./SuperInputTextOld.module.css";

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & { //  +       
    onChangeText?: (value: string) => void
    onEnter?: () => void
    error?: string | null
    spanClassName?: string
    buttonCallback?:(a:any)=>void
    title?:any
    needButton?:boolean
    type?:string | number
    placeholder?:string
};

const SuperInputTextOld: React.FC<SuperInputTextPropsType> = (
    {
        type, //          
        onChange, onChangeText,
        onKeyPress, onEnter,
        error,
        className, spanClassName,title,placeholder,
        buttonCallback,

        ...restProps//       restProps
    }
) => {


    const onChangeCallback = (e: ChangeEvent<HTMLInputElement> ) => {
        onChange //    onChange
        && onChange(e); //     ( onChange  )
        onChangeText && onChangeText(e.currentTarget.value);

    }



    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);
        e.key === "Enter" //    Enter
        && onEnter //    onEnter
        && onEnter(); //   
    }


    const finalSpanClassName = `${s.errorText} ${spanClassName ? spanClassName : ""}`;


    return (
        <span >
            <input
                type={type? type :'text'}
                onChange={onChangeCallback}
                onKeyPress={onKeyPressCallback}
                className={s.inputOld}
                value={title}
                placeholder={placeholder}
                {...restProps} //        (value   )
            />


            <div className={finalSpanClassName}>

                {error && <div className={finalSpanClassName}>{error}</div>}
            </div>
        </span>
    );
}

export default SuperInputTextOld;
